.info {
  grid-template-columns: 35% 65%;

  &__img {
    background-image: url("/images/info.png");
  }

  &__block {
    display: flex;

    padding: 96px 0 100px 68px;

    @media (max-width: 822px) {
      justify-content: space-around;
    }

    @media (max-width: 719px) {
      flex-direction: column;
      align-items: center;
      padding: 60px 0 0;
    }
  }

  &__item {
    margin-right: 80px;

    font-size: 14px;
    line-height: 23px;
    letter-spacing: -0.06em;
    color: #919ab7;

    @media (max-width: 719px) {
      margin-bottom: 60px;
      margin-right: 0;

      text-align: center;
    }
  }

  &__heading {
    margin-bottom: 60px;

    color: $c-text-main;
    text-transform: uppercase;

    @media (max-width: 719px) {
      margin-bottom: 20px;
    }
  }

  &__text {
    max-width: 155px;
  }

  &__link {
    display: inline-block;

    &--email {
      color: #006dff;
    }

    &:first-child {
      margin-bottom: 20px;
    }

    &:hover {
      color: #006dff;
    }
  }
}
